/** TRANSITIONS **/
.slide-fade-enter{
-webkit-transform: translateX(10px);
        transform: translateX(10px);
opacity: 0;
}
.slide-fade-enter-active ,
.slide-fade-leave-active{
-webkit-transition: all 0.2s ease;
transition: all 0.2s ease;
}
.slide-fade-leave-to{
-webkit-transform: translateX(-10px);
        transform: translateX(-10px);
opacity: 0;
}


