          align-items: center;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
}
.navegation {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 0 40px;
}
a {
  text-decoration: none;
  color: #ffffff;
  display: inline-block;
  position: relative;
  padding-bottom: 2px;
}
.mobile {
  display: none;
}
.navegation a:hover {
  color: #ffd249;
}
.navegation a:before {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  height: 2px;
  width: 0;
  -webkit-transition: width 0s ease, background 0.5s ease;
  transition: width 0s ease, background 0.5s ease;
}
.navegation a:after {
  content: "";
  display: block;
  position: absolute;
  right: 0;
  bottom: 0;
  height: 2px;
  width: 0;
  background: #ffd249;
  -webkit-transition: width 0.5s ease;
  transition: width 0.5s ease;
}
.navegation a:hover:before {
  width: 100%;
  background: #ffd249;
  -webkit-transition: width 0.5s ease;
  transition: width 0.5s ease;
}
.navegation a:hover:after {
  width: 100%;
  background: transparent;
  -webkit-transition: all 0s ease;
  transition: all 0s ease;
}
